@import url("index.css");

/* ProjectsCondensed */

#software-projects {
  margin-top: 250px;
  display: flex;
  flex-direction: column; 
  justify-content: center; 
  align-items: center; 
  width: 100%;
  min-height: 100vh;
}

.software-project-card {
  padding-top: 50px;
  margin: 0 auto;
  margin-left: 150px;
}

.software-project-condsened-body:hover {
  animation: bounceAll 0.6s ease-in-out;
}

.software-project-condsened-body {
  position: relative;
  width: 100%;
}

.software-project-icon-container {
  position: relative;
  width: 100%;
  margin-bottom: 25px;
}

.software-project-icon {
  width: 50%;
  height: auto;
}

.name,
.tagline {
  margin-bottom: 10px;
  color: #333;
}

.name {
  font-size: 1.5rem;
}

.tech-stack {
  color: #333;
  font-size: .8rem;
}

.learn-more-button {
  font-size: 15px; 
  font-family: "Quicksand";
  font-weight: normal; 
  text-decoration: none;
  border-radius: 20px;
  border-color: #3D52A0;
  background: transparent;
  transition: background 0.3s, transform 0.3s, box-shadow 0.3s;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 5px 20px;
  margin-right: 25px; 
  position: relative; 
  overflow: hidden; 
  color: #333;
  margin-top: 15px;
}

.learn-more-button:hover {
  background: rgba(255, 255, 255, 0.2);
  color: #3D52A0;
  transform: scale(1.05);
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.5); 
}

@media (max-width: 1024px) {
  .software-project-icon-container {
    margin-bottom: 15px;
  }
}

/* ProjectsExpanded */

.software-project-expanded {
  background-color: #9cafe2;
}

.software-project-expanded div {
  margin-bottom: 30px;
}

.modal-header {
  display: flex;
  justify-content: space-between; 
  align-items: flex-start;
  margin-top: 30px;
}

.carousel {
  flex: 1; 
}

.carousel img {
  max-width: 100%; 
  height: auto; 
  border-radius: 10px; 
}

.modal-header-text {
    flex: 1; 
    margin-left: 20px; 
}

.modal-header-text p,
.modal-header-text h2,
.modal-header-text h6 {
  margin-bottom: 10px; 
  font-size: 1rem;
}

.modal-header-text .usage-description {
  font-weight: normal; 
}

.modal-header-text .name-expanded {
  font-size: 2rem; 
}

.modal-header-text .tech-stack {
  color: #3D52A0; 
}

/* Carousel */
.carousel,
.carousel-root {
  margin: 0;
  padding: 0;
}

/* Awards */

.awards {
  margin-left: 270px;
  margin-top: -70px !important;
  position: relative;
}

.awards p {
  margin-bottom: 10px;
}

/* Key Features */
.key-features {
  border-radius: 15px;
  padding: 2%;
  background-color: #3D52A0;
}

.key-features li {
  margin-bottom: 10px;
  color: #9cafe2;
}

.key-features h3 {
  font-weight: 800;
  padding: 2%;
  color: #9cafe2;
}

.feature-text {
  color: #9cafe2;
  padding: 2%;
}

  .exit-button {
    font-size: 15px; 
    font-family: "Quicksand";
    font-weight: normal; 
    text-decoration: none;
    border-radius: 20px;
    border-color: #3D52A0;
    background: transparent;
    transition: background 0.3s, transform 0.3s, box-shadow 0.3s;
    padding: 5px 20px;
    position: absolute; 
    right: 250px;
    top: 60px; 
    overflow: hidden; 
    color: #333;
  }
  
  .exit-button:hover {
    background: rgba(255, 255, 255, 0.2);
    color: #3D52A0;
    transform: scale(1.05);
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.5); 
  }

  /* MODAL POPUP*/
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5); 
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000000;
  }
  
  /* Modal content */
  .card {
    background-color: var(--background);
  }
  
  .modal-content {
    border-radius: 35px;
    max-width: 70%;
    max-height: 90%;
    overflow-y: auto;
    background-color: #9cafe2;
  }
  
  body.modal-open {
    overflow: hidden; 
  }
  
  .modal-text {
    padding: 5% 10% 0% 10%;
  }
  
@media (max-width: 768px) {
  .modal-header {
    flex-direction: column; 
  }

  .modal-header-text {
    margin-left: 0; 
  }
}